.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  text-align: center;
  position: relative;
  min-height: 100vh; /* Ensure the container takes the full viewport height */
}

.App-header {
  padding: 20px;
}

.deck-wrapper {
  position: fixed; /* Fix the position to the viewport */
  bottom: 50px;       /* Align to the bottom of the viewport */
  right: 300px;        /* Align to the right of the viewport */
  margin: 50px;    /* Optional: Add some margin to avoid edges */
}


/* Play button styling */
.play-button {
  background-color: #4CAF50; /* Green background */
  border: none; /* Remove border */
  color: white; /* White text */
  padding: 15px 32px; /* Add padding */
  text-align: center;
  font-size: 16px; /* Adjust font size */
  margin: 20px; /* Margin around the button */
  cursor: pointer; /* Pointer cursor on hover */
  border-radius: 8px; /* Rounded corners */
}

/* Add a hover effect */
.play-button:hover {
  background-color: #45a049;
}


/* Center card slots */
.card-slots-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 50vh; /* Adjust height to ensure it's centered on the screen */
}

/* Each hand (row of 6 card slots) */
.hand {
  display: flex;
  justify-content: center;
  margin: 10px 0;
}

/* Each card slot */
.card-slot {
  width: 80px;  /* Width of each card slot */
  height: 120px; /* Height of each card slot */
  /* border: 2px solid #333; Border for the slot */
  border-radius: 8px;
  margin: 0 10px; /* Space between the slots */
  background-color: transparent; /* Light grey background for empty slots */
}

.card-slot:hover {
  transform: scale(1.1); /* Optional: scale up on hover for visual feedback */
}

.score-button {
  margin-right: 10px; /* Adjust spacing as needed */
  margin-top: 30px;
  cursor: pointer;
  padding: 5px 10px; /* Smaller padding for a smaller button */
  font-size: 18px; /* Smaller font size */
  border: 1px solid #ccc; /* Optional: Add a border */
  border-radius: 4px; /* Optional: Add some rounding to the corners */
  background-color: #f0f0f0; /* Background color */
  transition: background-color 0.1s; /* Smooth transition on hover */
  width: 200px; /* Set a fixed width */
  height: 75px; /* Set a fixed height */
}

.score-button:hover {
  background-color: #e0e0e0; /* Change background on hover */
}

.played-cards {
  display: flex;
  justify-content: center;
  margin-bottom: 10px; /* Space between played cards and hands */
  position: relative; /* Enable positioning for child elements */
}

.played-card {
  margin: 0 -30px; /* Negative margin to create overlap */
  transition: transform 0.3s; /* Optional: add a transition for smooth hover effect */
}

.played-card:hover {
  transform: scale(1.1); /* Optional: scale up on hover for visual feedback */
}


.card-slots-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.hand {
  display: flex;
  margin-bottom: 20px;
}

.additional-hand {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  display: flex;
  position: absolute;
  right: -250px; /* Adjust as needed for the desired right offset */
  top: -100px;     /* Adjust for the desired vertical offset */
}

.hand-label {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 8px;
  text-align: center;
  color: #dfdfdf; /* Adjust color as needed */
}

.card-row {
  display: flex;
  margin: 0 -20px; /* Spacing between cards */
}

/* Each card slot */
.card-slot-kitty {
  width: 80px;  /* Width of each card slot */
  height: 120px; /* Height of each card slot */
  /* border: 2px solid #333; Border for the slot */
  border-radius: 8px;
  margin: 0 -10px; /* Space between the slots */
  background-color: transparent; /* Light grey background for empty slots */
}

.card-slot-kitty:hover {
  transform: scale(1.1); /* Optional: scale up on hover for visual feedback */
}

.instruction-text {
  font-size: 0.8em;
  font-weight: 500;
  margin-bottom: 20px;
  color: #ffffff; /* Adjust color as needed */
  text-align: center;
}
